<template>
  <div>
    <Loadings v-if="list ==null"></Loadings>
    <div class="container scontainer" v-else>
      <div class="search-bar">
        <van-search
          v-model="keywords"
          show-action
          label="查询"
          placeholder="请输入搜索关键词"
          @search="searchData"
          clearable
        >
          <template #action>
            <div @click="searchData">搜索</div>
          </template>
        </van-search>
      </div>
      <Empty v-if=" list.length == 0"></Empty>
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
        class="list"
        v-else
      >
        <div class="item" v-for="(item, index) in list" :key="index">
          <div class="info">
            <div class="title">客户名称:{{item.cust_name}}</div>
            <div class="title">
              货号{{item.number}}
              <span style="padding-left: 20px;">品名{{item.title}}</span>
            </div>
            <div class="tips">型号:{{item.model == '' ? '暂无': item.model}}</div>
            <div class="tips">产品属性:{{item.attribute == '' ? '暂无': item.attribute}}</div>

            <div class="tips">
              规格:{{item.standards}}
              <span style="padding-left: 20px;">单位:{{item.unit}}</span>
            </div>
            <div class="tips">单价:{{item.price}}</div>
            <div class="tips">业务员:{{item.sname}}</div>
          </div>
        </div>
      </van-list>
    </div>
  </div>
</template>

<script>
import { quote } from "@api/public";
import { Icon, Search, Loading, List } from "vant";
import Empty from "@components/Empty";
import Loadings from "@components/Loadings";
export default {
  name: "Quote",
  components: {
    [Icon.name]: Icon,
    [Search.name]: Search,
    [Loading.name]: Loading,
    [List.name]: List,
    Empty,
    Loadings,
  },

  data: function () {
    return {
      list: null,
      keywords: "",
      page: "",
      loading: false,
      finished: false,
    };
  },
  created() {},
  mounted: function () {
    this.getData();
  },
  methods: {
    searchData() {
      if (this.keywords != "") {
        this.page = "";
      }
      this.getData();
    },

    getData() { 
      quote({
        search: this.keywords,
        page: this.page,
        length: 30,
      }).then((res) => {
        if (this.page != "") {
          this.list = this.list.concat(res.data.data);
        } else {
          this.list = res.data.data;
        }

        this.finished = res.data.data.length < 30 ? true : false;
        this.loading = res.data.data.length < 30 ? true : false;
        if (res.data.data.length == 30) {
          this.page = this.list[this.list.length - 1].pid;
        }
      });
    },
    onLoad() {
      this.getData();
    },
  },
};
</script>
<style scoped>
.container {
  box-shadow: 0 0 5px #eee;
  background-color: #fff;

  background: #fff;
  border-radius: 5px;
}

.container .caption {
  font-weight: 600;
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #eee;
  padding-left: 10px;

  font-size: 16px;
  color: #333;
}

.caption .month {
  height: 25px;
  width: 60px;
  background: #092e48;
  border-radius: 20px;
  color: #fff;
  line-height: 25px;
  font-size: 12px;
}

.caption .month span {
  display: inline-block;
  width: 23px;
  height: 23px;
  background: #fff;
  border-radius: 50%;
  margin-left: 1px;
  margin-right: 4px;
} 

.container .list {
  line-height: 43px;
  font-size: 14px;
}

.container .list .item {
  padding: 15px 0;
  display: block;
  padding-left: 8px;
  border-bottom: 1px solid #eee;
}

.container .list img {
  width: 65px;
  margin-right: 10px;
}

.container .list .info {
  line-height: 18px;
  color: #999;

  font-size: 12px;
}

.container .list .info .title {
  color: #333;
  font-size: 16px;
  margin: 5px 0;
}

.container .list .info .tips {
  margin: 5px 0;
}

.container .list .item .right {
  padding-right: 15px;
  font-weight: bold;
}

.container .list .item .right span {
  font-size: 10px;
  font-weight: 500;
}
</style>
